<template>
    <div :class=" col || 'col-sm-12'">

        <div class="form-group">

            <label :class="{'validation-text-red': hasErrorClass}">{{label}}
                <small>{{subLabel}}</small>
            </label>

            <slot></slot>

            <span class="text-danger" v-if="validate">
                <small>{{validate.first(name) || ""}}</small>
            </span>

        </div>

    </div>
</template>
<script>
    export default {
        props: [
            'errors',
            'validate',
            'label',
            'col',
            'name',
            'subLabel'
        ],
        computed: {
            hasErrorClass() {

                if (this.validate && this.validate.first(this.name)) {
                    return true;
                }

                // return !!(this.errors && errors[name]);

            },

        },

    }
</script>
<style scoped>
    span {
        display: block;
    }
</style>
