<template>
    <div class="messages-notifications os-dropdown-trigger os-dropdown-position-left" v-hover-class="'over'">
        {{user.photo_url}}
        <i class="os-icon os-icon-mail-14"></i>
        <div class="new-messages-count">
            {{notifications.length}}
        </div>
        <div class="os-dropdown light message-list">
            <ul>
                <li v-for="n in notifications">
                    <a href="#">
                        <div class="user-avatar-w">
                            <img alt="" :src="n.img">
                        </div>
                        <div class="message-content">
                            <h6 class="message-from">
                                {{n.from}}
                            </h6>
                            <h6 class="message-title">
                                {{n.message}}
                            </h6>
                        </div>
                    </a>
                </li>

            </ul>
        </div>
    </div>
</template>
<script>
    import AuthMixins from '../../mixins/auth'

    export default {
        mixins: [AuthMixins],
        data() {
            return {
                notifications: []
            }
        },
        mounted() {
            if (!this.user.photo_url) {
                this.notifications.push({
                    message: 'Você ainda não tem uma foto de perfil',
                    from: this.constants.configs.APP_NAME,
                    img: this.constants.configs.SYSTEM_IMG_PATH
                })
            }
            if (!this.user.phone_number) {
                this.notifications.push({
                    message: 'Você ainda não configurou um tel. ',
                    from: this.constants.configs.APP_NAME,
                    img: this.constants.configs.SYSTEM_IMG_PATH
                })
            }
        },
    }
</script>