import Vue from 'vue'
import Vuex from 'vuex'
import auth from './store/auth/index'
import company from './store/company/index'
import news from './store/news/index'
import program from './store/program/index'
import user from './store/user/index'
import market from './store/market/index'
import {BASE_URL} from './constants/configs'
import CONSTANTS    from './constants/index'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        API_URL:BASE_URL,
        preload:false,
        CONSTANTS,
    },
    mutations: {
        PRELOAD(state,value){
            state.preload = value
        }
    },
    actions: {
        preload(context,value){
            context.commit('PRELOAD',value);
        }
    },
    modules: {
        auth,
        company,
        user,
        program,
        market,
        news
    }
})
