import axios from "axios";

import {
    CREATE_PROGRAM_URL,
    GET_PROGRAMS_URL,
    GET_PROGRAM_URL,
    GET_COMPANY_PROGRAMS_URL,
    GET_ACTIVE_PROGRAMS_URL
} from '../../constants/urls'


export default {

    createProgram(context, data) {
        return axios.post(CREATE_PROGRAM_URL, data, {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
                'Content-Type': 'multipart/form-data'
            }
        });

    },
    pushProgram(context,data) {
        context.commit();
    },
    getPrograms(context){
        return axios.get( GET_PROGRAMS_URL,
           {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
            }
        }).then((response)=>{
            context.commit('GET_PROGRAMS',response.data.data);
        });
    },
    getActivePrograms(context){
        return axios.get( GET_ACTIVE_PROGRAMS_URL,
           {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
            }
        }).then((response)=>{
            context.commit('GET_PROGRAMS',response.data.data);
        });
    },
    companyPrograms(context,id){
        return axios.get( GET_COMPANY_PROGRAMS_URL(id),
           {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
            }
        }).then((response)=>{
            context.commit('GET_PROGRAMS',response.data.data);
        });
    },
    getProgram(context, params){

        let url =  GET_PROGRAM_URL(params.id);
        let header = {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
            }};
        return axios.get(url,header).then(response=>{
            context.commit("GET_PROGRAM",response.data.data);
        });
    }

}