import Vue from "vue";
import {ACTIVATE_USER_URL, GET_CREDENTIALS_URL} from '../../constants/urls'
import store from "../../store";
import router from "../../routes/router";

export default {
    localStorageSetItems(context, data) {
        localStorage.setItem("username", data.data.name);
        localStorage.setItem("email", data.data.email);
        localStorage.setItem("access_token", data.data.token);
    },
    localStorageUnsetItems() {
        localStorage.removeItem("username");
        localStorage.removeItem("email");
        localStorage.removeItem("access_token");
    },
    getCredentials(context) {
        Vue.prototype.axios
            .post(GET_CREDENTIALS_URL, {}, {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('access_token')}`,
                }
            })
            .then(response => {
                context.dispatch("setUser", response.data);
            }).catch(() => {
            store.dispatch('logout');
            router.push({name: 'home'});
        });
    },
    setUser(context, response) {
        context.commit("SET_USER", response);
    },
    setToken(context, token) {
        context.commit("SET_TOKEN", token);
    },
    createUser(context, data) {
        return Vue.prototype.axios
            .post(`${this.state.API_URL}/user`, data)
            .then(response => {
                context.dispatch("setUser", response);
            });
    },

    login(context, data) {
        return Vue.prototype.axios
            .post(`${this.state.API_URL}/login`, data)
            .then(response => {
                data.stay_logged ? context.dispatch("localStorageSetItems", response.data) : null;
                context.dispatch("setUser", response.data);
                context.dispatch("setToken", response.data.data.token);
                return response;
            });
    },
    setUserCompany(context, data) {
        context.commit('SET_USER_COMPANY', data)
    },
    getUser(context) {
        return Vue.prototype.axios
            .post(`${this.state.API_URL}/get-user`)
            .then(response => {
                context.dispatch("setUser", response.data);
            });
    },

    logout(context) {

        let token = localStorage.getItem('access_token') ? localStorage.getItem('access_token') : store.state.token;
        if (token) {
            context.dispatch("localStorageUnsetItems");

            return Vue.prototype.axios
                .post(`${this.state.API_URL}/logout`, {}, {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                }).then(r => {
                    console.log('logged out');
                    localStorage.clear();
                });
        } else {
            context.commit("LOGOUT");
        }
    },
    activateUser(context, code) {
        return Vue.prototype.axios
            .get(`${ACTIVATE_USER_URL}/${code}`);

    }

}