import {COMPANY_UPLOAD_LOGO_SIZE} from '../../../constants/configs'

export default {
    data: function () {
        return {
            company: {
                name: null,
                display_name: null,
                cnpj: null,
                email: null,
                phone_number: null,
                phone_mobile: null,
                site_url: null,
                photo_url: null,
                resume: null,
                description: null,
                address: null,
                address_number: null,
                address_complement: null,
                city_id: null,
                state_id: null,
                district: null,
                zip_code: null,
            },
            imageUpload: '',
            imagePreview: '',
            show: true,
            errors: {}
        }
    },
    methods:{
        fileChange(event) {
            this.errors.logotipo = "";
            let files = event.target.files || event.dataTranfer.files;

            if (files.length) {
                let file = files[0];
                if (file.size > COMPANY_UPLOAD_LOGO_SIZE) {

                    this.errors.logotipo = "o tamanho máximo da imagem não pode exceder 2 megabytes";
                    this.$refs.logotipo.value = ""

                } else {
                    this.imageUpload = file;
                    this.setImage(this.imageUpload);
                }

            } else {
                this.$snotify.error("Não há arquivos selecionados", "Erros!");
            }
        },
        setImage(file) {

            let reader = new FileReader();

            reader.readAsDataURL(file);

            reader.onload = e => {
                this.photo_url = e.target.result;
            };

        },
        getFormData() {
            let data = new FormData();

            for (let field in this.company) {
                if (field !== "photo_url" && field !== "state_id" && field !== "city_id") {
                    data.append(field, this.company[field]);
                }
            }

            let state_id = this.company.state_id.hasOwnProperty('value') ? this.company.state_id.value : this.company.state_id;
            data.append('state_id', state_id);

            let city_id = this.company.city_id.hasOwnProperty('value') ? this.company.city_id.value : this.company.city_id;
            data.append('city_id', city_id);

            data.append('slug', this.slugfy(this.company.display_name));

            this.imageUpload && data.append("photo_url", this.imageUpload);

            return data;

        },
    }
}