<template>
    <div class="modal fade" tabindex="-1" role="dialog" :id="id">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header bg-primary">
                    <h5 class="modal-title">Convidar usuário</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="form-group">
                                <label for="email" class="element-header"> Email:</label>
                                <input class="form-control" placeholder="Email do novo membro" type="email"
                                       v-model="email" id="email">
                            </div>
                        </div>
                        <div class="col-md-12">
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button class="btn btn-primary" @click="submit"><i class="os-icon os-icon-ui-22"></i> convidar
                    </button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">cancelar</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {INVITE_URL} from '../../../constants/urls'

    export default {
        props: {
            id: {
                default: 'form-invitation',
                type: String
            }
        },
        data() {
            return {
                email: 'r4free@hotmail.com',
                errors: null
            }
        },
        methods: {
            submit() {
                $('.modal').modal('hide');
                this.$snotify.async('Enviando o email', 'Convidando', () => {
                    return axios
                        .post(INVITE_URL, {email: this.email}, {
                            headers: {
                                Authorization: `Bearer ${this.$store.state.auth.token}`,
                            }
                        })
                        .then((r) => {
                            return {
                                title: 'Sucesso!!!',
                                body: 'Convite enviado com sucesso',
                                configs: {
                                    closeOnClick: true,
                                    position: 'rightBottom'

                                }
                            }
                        })
                        .catch((e) => {
                            return {
                                title: 'Erro!!!',
                                body: e.response.data.errors.email[0],
                                config: {
                                    type: 'error',
                                    closeOnClick: true,
                                    position: 'rightBottom'
                                }
                            }
                        })
                },{
                    closeOnClick: true,
                        position: 'rightBottom'

                });
            }
        },
    }
</script>
<style scoped lang="scss">
    .modal-header {
        h5.modal-title, button > span {
            color: white;
        }
    }
</style>
