                <img :alt="user.photo_url" :src="user.photo_url || 'user.png'">
            </div>
            <div class="logged-user-info-w">
                <div class="logged-user-name">
                    <!--{{user.name}}-->
                </div>
                <div class="logged-user-role text-capitalize">
                    <!--{{user.role.name}}-->
                </div>
            </div>
