import {USER_UPDATE} from '../../constants/urls'
export default {
    updateUser(context,data){
        return axios.post(USER_UPDATE(this.state.auth.user.id),data,{
            headers: {
                Authorization: `Bearer ${localStorage.getItem('access_token')}`,
                'Content-Type': 'multipart/form-data'
            }
        });
    }
}