import Site from '../views/site/SiteIndex'

export default {

    path: '/',
    component: Site,
    children: [
        {
            path: '',
            name: 'home',
            component: () => import('../views/site/Home'),
            meta: {}
        },{
            path: '/cadastrar',
            name: 'register',
            component: () => import('../views/site/Register'),
            meta: {}
        },
        {
            path: '/about',
            name: 'about',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ '../views/site/About.vue'),
            meta: {}
        },
        {
            path: '/entrar',
            name: 'login',
            component: () => import('../views/site/Login'),
            meta: {}
        },
        {
            path: '/programas',
            name: 'program.index',
            component: () => import('../views/site/programs/Index'),
            meta: {}
        },
        {
            path: '/programas/:id/mostrar',
            name: 'program.show',
            component: () => import('../views/site/programs/Show'),
            meta: {}
        },
        {
            path: '/news',
            name: 'news.index',
            component: () => import('../views/site/news/Index'),
            meta: {}
        },
        {
            path: '/news/:id/',
            name: 'news.show',
            component: () => import('../views/site/news/Show'),
            meta: {}
        },
        {
            path: '/empresas',
            name: 'company.index',
            component: () => import('../views/site/company/Index'),
            meta: {}
        },
        {
            path: '/empresas/:id/mostrar',
            name: 'company.show',
            component: () => import('../views/site/company/Show'),
            meta: {}
        },
        {
            path: '/ativar-conta',
            name: 'account.activate',
            component: () => import('../views/site/ActivateAccount'),
            meta: {}
        },
        {
            path: '/concluir-cadastro/:token/',
            name: 'invite.finish',
            component: () => import('../views/site/InviteRegister'),
            meta: {}
        },


    ].map((route) => {
        route.meta.namespace = 'site';
        return route;
    })

}