import {GET_COMPANIES_URL,GET_COMPANY_URL,UPDATE_COMPANY_URL,CREATE_COMPANY_URL} from '../../constants/urls';

export default {

    createCompany(context, data) {
        return axios.post(CREATE_COMPANY_URL, data, {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
                'Content-Type': 'multipart/form-data'
            }
        });
    },
    updateCompany(context, data) {

        return axios.post(UPDATE_COMPANY_URL(this.state.auth.user.company_id), data, {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
                'Content-Type': 'multipart/form-data'
            }
        });
    },

    getCompanies(context) {
        return axios.get(`${GET_COMPANIES_URL}/`).then((response)=>{
            context.commit('GET_COMPANIES',response.data.data);
        });
    },
    getCompany(context,id) {
        return axios.get(GET_COMPANY_URL(id)).then((response)=>{
            context.commit('GET_COMPANY',response.data.data);
        });
    }

}