<template>
    <li class="" :class="{'has-sub-menu':submenu}" v-hover-class="'active'">
        <router-link :to="path">
            <div class="icon-w" v-if="iconClass">
                <div :class="iconClass"/>
            </div>
            <span>{{text}}</span>
        </router-link>

            <slot v-if="submenu"/>

    </li>
</template>
<script>
    import SubHeader from './MenuSubHeader'

    export default {
        components: {
            SubHeader
        },
        props: {
            path: {
                type:Object,
                default: ()=>{return {}}
            },
            text: {
                default: ''
            },
            submenu: {
                type: Boolean,
                default: false
            },
            iconClass: {
                type: String,
                default: "",
            },
            classes: {
                type: Object,
                default: () => {
                    return {}
                }
            }
        },
    }
</script>
