<template>
    <form @submit.prevent="submit">
        <form-group :validate="vee" label="Email">
            <input type="text" class="form-control" name="email" :value="$store.state.auth.user.email" disabled/>
        </form-group>

        <form-group :validate="vee" label="Nome" name="name">
            <input type="text" class="form-control" name="name" v-model="user.name" v-validate="'required'"/>
        </form-group>

        <form-group :validate="vee" label="Telefone" name="phone_number">
            <input class="form-control" type="text" v-model="user.phone_number" v-mask="constants.masks.DDD_PHONE_MOBILE" v-validate="'required|min:14|max:15'" name="phone_number">
            <span class="text-danger" v-if="errors" v-for="e in errors.phone_number">
                            <small>{{e}}</small>
            </span>
        </form-group>

        <form-group :validate="vee" label="Imagem" name="photo_url">
            <input type="file" name="photo_url" @change="fileChange" ref="photo_url" class="form-control" />
            <span class="text-danger" v-if="errors" v-for="e in errors.photo_url">
                   <small>{{e}}</small>
            </span>
        </form-group>
        <!--<img class="img-fluid" :src="imagePreview"/>-->
        <div class="col-md-12">
            <input type="submit" value="Salvar" class="btn btn-primary float-right">
        </div>

    </form>
</template>

<script>

    import {COMPANY_UPLOAD_LOGO_SIZE} from "../../../constants/configs";
    import handleErrors from '../../../mixins/handle_errors'

    export default {
        mixins:[handleErrors],
        data() {
            return {
                user: {
                    name: '',
                    phone_number: '',
                    photo_url: ''
                },
                imageUpload: '',
                imagePreview: '',
                errors: {},
            }
        },
        methods: {
            getFormData() {

                let data = new FormData();

                for (let field in this.user) {
                    field !== "photo_url" && (data.append(field, this.user[field]));
                }

                this.imageUpload && data.append("photo_url", this.imageUpload);

                return data;

            },
            fileChange(event, file_size = COMPANY_UPLOAD_LOGO_SIZE) {

                this.errors.photo_url = "";

                let files = event.target.files || event.dataTranfer.files;

                if (files.length) {
                    let file = files[0];

                    if (file.size > file_size) {

                        this.errors.photo_url = "o tamanho máximo da imagem não pode exceder 2 megabytes";

                    } else {
                        this.imageUpload = file;
                        this.setImage(this.imageUpload);
                    }

                } else {
                    this.$snotify.error("Não há arquivos selecionados", "Erros!");
                }
            },
            setImage(file) {

                let reader = new FileReader();

                reader.readAsDataURL(file);

                reader.onload = e => {
                    this.imagePreview = e.target.result;
                    this.photo_url = this.imagePreview;
                };

            },
            submit() {
                this.$validator.validateAll().then((validate) => {
                    if (validate){
                        let data = this.getFormData();
                        this.preload(true);
                        data.append("_method", 'PUT');
                        this.$store.dispatch('updateUser', data).then((response)=>{
                            this.$store.dispatch('getCredentials');
                            this.$snotify.success('Dados atualizados', 'Sucesso');
                        }).catch(e => {
                                this.$snotify.error('Preencha corretamente os campos para enviar', 'Erro');
                            }
                        ).finally(() => this.preload(false));
                    }
                }).catch(() => {
                    return false
                });

            },
        },
        created() {
            this.user.name = this.$store.state.auth.user.name;
            this.user.phone_number = this.$store.state.auth.user.phone_number;
        }
    }
</script>