<template>
    <form @submit.prevent="submit" enctype="multipart/form-data">
        <form-wizard @on-complete="onComplete" nextButtonText="próximo" backButtonText="anterior" ref="formWizard"
                     finishButtonText="finalizar" color="#3066e8" title="Cadastro de empresa"
                     subtitle="Falta pouco para você concluir seu cadastro e poder utilizar a plataforma">
            <tab-content title="Empresa" :before-change="validateSlide1">
                <div class="row padded-v" ref="firstTab">
                    <form-group :validate="vee" label="Razão Social" name="name" sub-label=" (obrigatório)">
                        <input class="form-control" type="text" v-model="company.name" v-validate="'required|min:10'"
                               autofocus
                               name="name"/>
                    </form-group>

                    <form-group :validate="vee" label="Nome fantasia" name="display_name"  sub-label=" (obrigatório)">
                        <input class="form-control" type="text" v-model="company.display_name"
                               v-validate="'required|max:200'" name="display_name">
                    </form-group>

                    <form-group :validate="vee" label="CNPJ" name="cnpj" :errors="errors"  sub-label=" (obrigatório)">
                        <input class="form-control" name="cnpj" v-mask="constants.masks.CNPJ"
                               v-validate="'required|min:18|max:18'"
                               type="text" v-model="company.cnpj" ref="cnpj" >
                        <span class="text-danger" v-if="errors" v-for="e in errors.cnpj">
                            <small>{{e}}</small>
                        </span>
                    </form-group>
                </div>
            </tab-content>

            <tab-content title="Contato" :before-change="validateSlide2">
                <div class="row padded-v">

                    <form-group :validate="vee" label="Email" name="email"  sub-label=" (obrigatório)">
                        <input class="form-control" name="email" v-validate="'required|email'"
                               type="email" v-model="company.email">
                        <span class="text-danger" v-if="errors" v-for="e in errors.email">
                            <small>{{e}}</small>
                        </span>

                    </form-group>

                    <form-group :validate="vee" label="Telefone Móvel" name="phone_mobile" col="col-md-6" sub-label="(opcional)">
                        <input class="form-control" type="text" v-model="company.phone_mobile"
                               v-mask="constants.masks.DDD_PHONE_MOBILE">
                        <span class="text-danger" v-if="errors" v-for="e in errors.phone_mobile">
                            <small>{{e}}</small>
                        </span>
                    </form-group>

                    <form-group :validate="vee" label="Telefone Fixo" name="phone_number" col="col-md-6"  sub-label=" (obrigatório)">
                        <input class="form-control" name="phone_number" v-validate="'required|max:15|min:14'"
                               v-mask="constants.masks.DDD_PHONE"
                               type="text" v-model="company.phone_number">
                        <span class="text-danger" v-if="errors" v-for="e in errors.phone_number">
                            <small>{{e}}</small>
                        </span>
                    </form-group>

                    <form-group :validate="vee" label="Website" name="site_url"  sub-label=" (obrigatório)">
                        <input class="form-control" v-validate="'required'" type="text" v-model="company.site_url"
                               name="site_url">
                        <span class="text-danger" v-if="errors" v-for="e in errors.site_url">
                            <small>{{e}}</small>
                        </span>
                    </form-group>

                    <form-group :validate="vee" label="Logotipo" name="logotipo" :errors="errors"  sub-label=" (obrigatório)">
                        <input class="form-control" type="file" required @change="fileChange" v-validate="'required'"
                               name="logotipo" ref="logotipo">
                        <span class="text-danger" v-if="errors" v-for="e in errors.photo_url">
                            <small>{{e}}</small>
                        </span>
                    </form-group>
                    <!--<img :src="imagePreview" alt="" class="img-fluid">-->

                </div>
            </tab-content>
            <tab-content title="Endereço">
                <div class="row padded-v">
                    <form-group :validate="vee" label="Estado" name="state" col="col-md-6"  sub-label=" (obrigatório)">
                        <v-select v-model="company.state_id" :options="states" v-validate="'required'" name="state">
                            <span slot="no-options">{{constants.messages.MULTSELECT_SEARCH_MESSAGE}}</span>
                        </v-select>
                    </form-group>

                    <form-group :validate="vee" label="Cidade" name="city" col="col-md-6"  sub-label=" (obrigatório)">
                        <v-select v-model="company.city_id" :options="cities" v-validate="'required'" name="city">
                            <span slot="no-options">{{constants.messages.MULTSELECT_SEARCH_MESSAGE}}</span>
                        </v-select>
                    </form-group>

                    <form-group :validate="vee" label="Bairro" col="col-md-6" name="district"  sub-label=" (obrigatório)">
                        <input class="form-control" type="text" name="district" v-model="company.district"
                               v-validate="'min:3|max:100|required'">
                        <span class="text-danger" v-if="errors" v-for="e in errors.district">
                            <small>{{e}}</small>
                        </span>
                    </form-group>

                    <form-group :validate="vee" label="CEP" col="col-md-6" name="zip_code"  sub-label=" (obrigatório)">
                        <input class="form-control" v-mask="constants.masks.CEP" v-validate="'required|min:9|max:9'"
                               type="text" v-model="company.zip_code" name="zip_code" >
                        <span class="text-danger" v-if="errors" v-for="e in errors.zip_code">
                            <small>{{e}}</small>
                        </span>
                    </form-group>

                    <form-group :validate="vee" label="Endereço" name="address"  sub-label=" (obrigatório)">
                        <input class="form-control" name="address" v-validate="'min:10|max:100|required'"
                               type="text" v-model="company.address">
                        <span class="text-danger" v-if="errors" v-for="e in errors.address">
                            <small>{{e}}</small>
                        </span>
                    </form-group>


                    <form-group :validate="vee" label="Número" name="address_number" col="col-md-6"  sub-label=" (obrigatório)">
                        <input class="form-control" type="number" v-model="company.address_number"
                               v-validate="'required'" name="address_number">
                    </form-group>

                    <form-group :validate="vee" label="Complemento" name="address_complement" col="col-md-6" sub-label=" (opcional)">
                        <input class="form-control" name="address_complement" type="text"
                               v-model="company.address_complement">
                    </form-group>

                </div>
                <input type="hidden" name="slug">

            </tab-content>

            <tab-content title="Sobre">
                <div class="row padded-v">

                    <form-group :validate="vee" label="Descrição" name="description"  sub-label=" (opcional)">
                        <input class="form-control" type="text" v-model="company.description">
                    </form-group>

                    <form-group :validate="vee" label="Resumo" name="resume"  sub-label=" (opcional)">
                        <textarea class="form-control" v-model="company.resume">

                        </textarea>

                    </form-group>

                </div>
            </tab-content>


        </form-wizard>
    </form>
</template>

<script>
    import vSelect from 'vue-select'
    import {FormWizard, TabContent} from 'vue-form-wizard'
    import 'vue-form-wizard/dist/vue-form-wizard.min.css'
    import WizardOptions from './wizard-options'
    import {COMPANY_UPLOAD_LOGO_SIZE} from '../../../constants/configs'
    import slugfy from '../../../helpers/slugfy'
    import StateCitiesMethods from '../../../mixins/cities_states'
    import FormMixins from './CommonMixin';

    export default {
        mixins:[StateCitiesMethods,FormMixins],
        components: {
            vSelect,
            FormWizard,
            TabContent,
        },
        methods: {

            slugfy,
            submit() {

                this.preload(true);

                this.$store.dispatch('createCompany', this.getFormData()).then(response => {
                    this.$store.dispatch('setUser',response);
                    this.$snotify.success('Empresa cadastrada!', 'Sucesso');
                }).catch(e => {
                        if (e.response.status === 422) {

                            this.errors = e.response.data.errors;

                            this.$validator.validateAll();

                            this.$refs.formWizard.reset();

                        }
                        else if(e.response.status === 401){
                            this.$snotify.error(e.response.data.message || 'não autorizado');
                        }
                        else if(e.response.status === 403){
                            this.$snotify.error(e.response.data.message );
                        }

                        else if (e.response.status === 500) {

                            this.$snotify.error('Erro no servidor');

                        } else {
                            this.$store.dispatch('logout');
                        }

                    }
                ).finally(()=>this.preload(false));
            },
            onComplete() {
                this.$validator.validate().then(result => {
                    if (!result) {
                        this.$refs.formWizard.reset();
                        this.$snotify.warning(this.constants.messages.FORM_VALIDATE_MESSAGE);
                    } else {
                        this.submit();
                    }
                })
            },
            validateSlide1() {

                let name = this.$validator.validate('name',this.name);
                let display_name = this.$validator.validate('display_name',this.display_name);
                let cnpj = this.$validator.validate('cnpj',this.cnpj);

                return true;

            }
            ,
            validateSlide2() {

                let email = this.$validator.validate('email');
                let site_url = this.$validator.validate('site_url');
                let logotipo = this.$validator.validate('logotipo');
                let phone_number = this.$validator.validate('phone_number');

                return true;

            }
            ,
        },
        mounted() {
            this.getStates()
        },
        watch:{
          state_id(){
              this.getCitiesByStateId(this.state_id.value).then(
                  ()=>this.company.city_id = ''
              )
          }
        },
        computed:{
            state_id(){
                return this.company.state_id
            },

        }

    }
</script>
<style>
    .wizard-btn, .vue-form-wizard i{
        font-family: sans-serif;
    }
    img {
        transition: all 1s;
    }

    .row.padded-v {
        margin-top: 50px;
    }

    .dropdown.v-select.single.searchable button span, .dropdown-toggle::after {
        display: none;
    }

</style>