export default {
    methods:{
        handleErrors(e){
            if(e.response && e.response.status){
                if (e.response.status === 422) {
                    this.errors = e.response.data.errors;
                }
                else if(e.response.status === 401){
                    this.$snotify.error(e.response.data.message || 'não autorizado');
                }
                else if(e.response.status === 403){
                    this.$snotify.error(e.response.data.message );
                }
                else if(e.response.status === 404){
                    this.$snotify.error(e.response.data.message || 'Não encontrado' );
                }
                else if(e.response.status === 503){
                    this.$snotify.error(e.response.data.message || 'Servidor em manutenção' );
                }
                else if (e.response.status === 500) {
                    this.$snotify.error('Erro no servidor');
                }
            } else{
                this.$snotify.error('Erro no servidor');
            }
        }
    }
}