export default {
    methods:{
        logout(){
            this.$router.push({name:'home'});
            this.$store.dispatch('logout');
        }
    },
    computed:{
        isAuthenticated(){
            return this.$store.state.auth.authenticated;
        },
        hasCompany(){
            return !!this.$store.state.auth.user.company_id;
        },
        user(){
            return !!this.$store.state.auth.user;
        }
    }
}