import Vue from 'vue'
import Router from 'vue-router'

import store from "../store";


import admin from './admin'
import site from './site'

Vue.use(Router)

let router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        site,
        admin,
    ]
});

router.beforeEach((to, from, next) => {

    /*GUARDA A ULTIMA ROTA*/
    let lr = to.name !== 'login' && localStorage.setItem('lr', to.name);
    to.params.id && localStorage.setItem('id',to.params.id);

    /*SE O USUÁRIO NÃO ESTÁ LOGADO MAS MANTEM O TOKEN PEGUE AS CREDECIAIS PELO TOKEN E DEIXE ACESSAR*/
    if (localStorage.getItem('access_token') && !store.state.auth.authenticated) {
        store.dispatch('getCredentials').then(() => {
            next();
        }).catch(e => {
            store.dispatch('logout');
            router.push({name: 'home'})
        });
    }

    if (
        (to.meta.auth && !store.state.auth.authenticated) ||
        (to.matched.some(record => record.meta.auth) &&
            !store.state.auth.authenticated)
    ) {
        return router.push("/entrar");
    } else {
        /*RETORNA PARA ADMIN CASO TENTE ACESSAR A TELA DE LOGIN ESTANDO LOGADO*/
        if (to.name === "login" && store.state.auth.authenticated) {
            return router.push({name: "admin.index"});
        }

        /*CASO NÃO ESTEJA VINCULADO  UMA EMPRESA O USÚARIO É DIRECIONADO A TELA INICIAL ONDE TEM O MODAL PARA CONCLUSÃO*/
        if (
            store.state.auth.authenticated
            && to.name !== "admin.index"
            && to.meta.namespace === "admin"
            && !store.state.auth.user.company_id
        ) {
            return router.push({name: "admin.index"});
        }

    }
    next();
});

export default router;