import axios from "axios";

import {
    CREATE_NEWS_URL,
    GET_ALL_NEWS_URL,
    GET_NEWS_URL,
    GET_COMPANY_NEWS_URL,
    UPDATE_NEWS_URL
} from '../../constants/urls'

export default {

    createNews(context, data) {
        return axios.post(CREATE_NEWS_URL, data, {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
                'Content-Type': 'multipart/form-data'
            }
        });

    },
    updateNews(context, params) {
        let data = params.data;
        data.append('_method','PUT');
        return axios.post(UPDATE_NEWS_URL(params.id), data, {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
                'Content-Type': 'multipart/form-data'
            }
        });

    },
    getAllNews(context){
        return axios.get( GET_ALL_NEWS_URL,
            {
                headers: {
                    Authorization: `Bearer ${this.state.auth.token}`,
                }
            }).then((response)=>{
            context.commit('GET_ALL_NEWS',response.data.data);
        });
    },
    companyNews(context,company_id){
        return axios.get( GET_COMPANY_NEWS_URL(company_id),
            {
                headers: {
                    Authorization: `Bearer ${this.state.auth.token}`,
                }
            }).then((response)=>{
            context.commit('GET_ALL_NEWS',response.data.data);
        });
    },
    getNews(context, id){

        let url =  GET_NEWS_URL(id);
        let header = {
            headers: {
                Authorization: `Bearer ${this.state.auth.token}`,
            }};
        return axios.get(url,header).then(response=>{
            context.commit("GET_NEWS",response.data.data);
        });
    }

}