import axios from 'axios'
export default {
    data() {
        return {
            states: [],
            cities: [],
        }
    },
    methods: {
        getCitiesByStateId(state_id,vSelect = true) {
            return axios
                .get(this.constants.urls.GET_CITIES_BY_STATE_ID_URL(state_id))
                .then(response => {
                    if(vSelect){
                        this.cities = response.data.data.map((obj) => {
                            return {
                                value: obj.id,
                                label: obj.name
                            }
                        });
                    }else{
                        this.cities = response.data;
                    }
                    return response
                });
        },
        getStates(vSelectFormat = true) {
           return axios
                .get(this.constants.urls.GET_STATES_URL)
                .then(response => {
                    if (vSelectFormat) {
                        this.states = response.data.data.map((obj) => {

                            return {
                                value: obj.id,
                                label: obj.name
                            }
                        });
                    } else {
                        this.states = response.data
                    }
                });
        }
    },
    computed: {},

}