<template>
    <li>
        <router-link :to="path">
            <strong class="badge badge-danger" v-if="badgeTextLeft">{{badgeTextLeft}}</strong>
            {{text}}
            <strong class="badge badge-danger" v-if="badgeTextRight">{{badgeTextRight}}</strong>
        </router-link>
    </li>
</template>
<script>
    export default {
        props: {
            text: {
                type: String,
                default: '',
                required:true
            },
            path: {
                type: Object,
            },
            badgeTextLeft: {
                type: String,
                default: ''
            },
            badgeTextRight: {
                type: String,
                default: ''
            }
        }
    }
</script>