import Admin from '../views/admin/AdminIndex'
import UserIndex from '../views/admin/user/Index'

import Dashboard from '../views/admin/dashboard/Index'
import AuthEdit from '../views/admin/auth/Edit'

export default {
    path: '/admin',
    component: Admin,
    meta: {
        auth: true,
        namespace: 'admin'
    },
    children: [
        {
            name: 'admin.index',
            path: '/',
            component: Dashboard,
            meta: {}
        },
        /*AUTH*/
        {
            name: 'auth.edit',
            path: '/editar/cadastro',
            component: AuthEdit,
            meta: {}
        },
        {
            name: 'user.index',
            path: 'usuarios',
            component: UserIndex,
            meta: {}
        },
        /*COMPANY*/
        {
            name: 'company.create',
            path: '/cadastrar/empresa',
            component: () => import('../views/admin/company/Create'),
            meta: {}
        }, {
            name: 'company.edit',
            path: '/editar/empresa',
            component: () => import('../views/admin/company/Edit'),
            meta: {}
        },
        /* PROGRAMS */
        {
            name: 'program.create',
            path: '/cadastrar/programa',
            component: () => import('../views/admin/program/Create'),
            meta: {}
        },
        {
            name: 'admin.program.index',
            path: 'programas/',
            component: () => import('../views/admin/program/Index'),
            meta: {}
        },
        {
            name: 'admin.program.subscribe',
            path: 'programas/inscrever-se/:id',
            component: () => import('../views/admin/program/subscribe/Subscribe'),
            meta: {}
        },
        /* NEWS */
        {
            name: 'news.create',
            path: '/cadastrar/noticia',
            component: () => import('../views/admin/news/Create'),
            meta: {}
        },
        {
            name: 'admin.news.index',
            path: '/minhas-noticias',
            component: () => import('../views/admin/news/Index'),
            meta: {}
        },
        {
            name: 'admin.news.edit',
            path: '/editar/noticia/:id',
            component: ()=>import('../views/admin/news/Edit'),
            meta:{}
        },
        /*User*/
        {
            name: 'admin.user.company',
            path: '/gerenciar-empresas',
            component: () => import('../views/admin/user/Company'),
            meta: {}
        },

    ].map((route) => {
        route.meta.namespace = 'admin';
        return route;
    })
}