<template>

    <ul>
        <li v-if="isAdmin">
            <router-link :to="{name:'home'}">
                <i class="dashicons dashicons-admin-site"></i><span>Site</span></router-link>
        </li>

        <li v-else>
            <router-link :to="{name:'admin.index'}">
                <i class="os-icon os-icon-mail-01"></i><span>Admin</span></router-link>
        </li>

        <li>
            <router-link :to="{name:'auth.edit'}">
                <i class="fas fa-user-edit"></i>
                <span>Editar Perfil</span>
            </router-link>
        </li>

        <li>
            <router-link :to="{name:'admin.user.company'}">
                <i class="fas fa-edit"></i>
                <span>Gerenciar Empresas</span>
            </router-link>
        </li>

        <li>
            <a href="#" @click.prevent="logout">
                <i class="fas fa-power-off"></i>
                <span>Sair</span>
            </a>
        </li>

    </ul>

</template>
<script>

    import AuthMixins from '../mixins/auth'

    export default {
        mixins: [AuthMixins],
        computed: {
            isAdmin() {
                return this.$route.meta.namespace === "admin";
            }
        },
    }
</script>